import React, { Component } from "react";
import { ErrorAlert } from "./Alert";

export default class NumberOfEvents extends Component {
  state = {
    eventNumber: 32,
    errorText: "",
  };

  handleInputChanged = (event) => {
    const value = event.target.value;
    this.setState({ eventNumber: value });
    if (value <= 0) {
      this.props.updateEvents(null, null, 0);
      console.log(value);
      this.setState({
        errorText: "Number should be at least 1!",
        eventNumber: 0,
      });
    } else {
      console.log(value);
      this.props.updateEvents(null, null, value);
      this.setState({
        errorText: "",
      });
    }
  };

  render() {
    return (
      <div className="numberOfEvents">
        <div className="alerts">
          <ErrorAlert text={this.state.errorText} />
        </div>
        <label>Events: </label>
        <input className="number-of-events" type="number" value={this.state.eventNumber} onChange={this.handleInputChanged} />
      </div>
    );
  }
}
